<template>
  <div style="height:100%;overflow: hidden;position:relative" class="preview_box">
    <div style="padding:12px  0 12px 50px;border-bottom:1px solid #e5e5e5;font-size:12px;background:#49B4E3;color:#fff" class="header">

      <span style="font-size:32px;font-weight: bold;">{{shareData.company || 'U-Mail'}}</span>

    </div>
    <div style="position:absolute;bottom:0;left:0;right:0;top:80px;overflow:auto;" v-loading="loading">
      <div class="content">
        <div class="share-info" v-if="is_share">
           <span class="share-user">{{shareData.username}}</span>
           <span>{{lan.SHARE_FROM}}</span>
        </div>

        <div class="share-box">
           <div class="file-info" v-if="is_share">
               <span class="file-big-icon" :class="className"></span>
               <h4 class="filename" :title="shareData.filename">{{shareData.filename}}</h4>
           </div>
          <div class="file-invalid" v-if="is_cancel">
               <span class="round">X</span>
               <h4 class="filename">{{lan.SHARE_CANCEL}}</h4>
           </div>
           <div class="file-pwd" v-if="is_share">
               <div class="form-item">
                   <p class="error">{{lan.SHARE_CODE_ERR}}</p>
                   <input type="text" v-model.trim="share_code" class="u-input j-pwd" :placeholder="lan.SHARE_INPUT_CODE">
                   <button class="u-btn j-check-pwd" @click="downloadShare">{{lan.SHARE_DOWNLOAD}}</button>
               </div>
           </div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
  import lan from '@/assets/js/lan';
  import {getShare,downloadShare} from '@/api/api';
  export default  {
    data(){
      return{
        loading:false,
        is_share:true,
        is_cancel:false,
        share_code:'',
        className:'',
        shareData:{
          filename:'',
          filetype:'',
          username:'',
          company:''
        }

      }
    },
    methods:{
      getShare(){
        getShare({ucode:this.$route.query.ucode}).then(res=>{
          this.shareData = res.data

          if(res.data.filetype == 'folder'){
            this.className = 'folder_type'
          }else{
            let aname = res.data.filename;
            let index = aname.lastIndexOf('.');
            let type = aname.slice(index+1);
            this.className = "icon-big-"+type;
          }
          this.is_share = true;
          this.is_cancel = false;
        }).catch(err=>{
          this.is_share = false;
          this.is_cancel = true;
        })
      },
      downloadShare(){
        if(!this.share_code && this.share_code.length==0){
          this.$message({ message: this.lan.SHARE_INPUT_CODE, type: 'error' });
          return;
        }
        let obj = {
          ucode: this.$route.query.ucode,
          code: this.share_code
        }
        this.loading = true;
        downloadShare(obj).then(response=>{
          this.loading = false;
          let blob = new Blob([response.data], { type: response.headers["content-type"] })
          let objUrl = URL.createObjectURL(blob);
          // let filenameHeader = response.headers['content-disposition']
          let filename = this.shareData.filename;
          if(this.shareData.filetype == 'folder'){
            filename += '.zip'
          }
          if (window.navigator.msSaveOrOpenBlob) {
            // if browser is IE
            navigator.msSaveBlob(blob, filename);//filename文件名包括扩展名，下载路径为浏览器默认路径
          } else {
            // var encodedUri = encodeURI(csvContent);//encodeURI识别转义符
            let link = document.createElement("a");
            link.setAttribute("href", objUrl);
            link.setAttribute("download", filename);

            document.body.appendChild(link);
            link.click();
          }
          this.$message({ message: this.lan.COMMON_DOWNLOAD_SUCCESS, type: 'success' });
        }).catch(err=>{
          var reader = new FileReader()
          reader.onload = e => {
            var err = JSON.parse(e.target.result)
            let str = '';
            if(err && err.detail){
              str = err.detail
            }
            if(err && err.non_field_errors){
              str = err.non_field_errors
            }
            this.$message({ message: this.lan.COMMON_DOWNLOAD_FAILED+'! ' +str, type: 'error' });
            }
          reader.readAsText(err,['utf-8'])
          this.loading = false;

        })
      }

    },
    created(){
      if(this.$route.query.ucode){
        this.getShare();
      }

    },
    beforeRouteUpdate (to, from, next) {
      // just use `this`
      next()
      if(this.$route.query.ucode){
        this.getShare();
      }
    },
    computed:{
      lan:function(){
        let lang = lan.zh
        if(this.$store.getters.getLanguage=='zh-hans'){
          lang = lan.zh
        }else if(this.$store.getters.getLanguage=='zh-tw'){
          lang = lan.zh_tw
        }else if(this.$store.getters.getLanguage=='en'){
          lang = lan.en
        }else if(this.$store.getters.getLanguage=='es'){
          lang = lan.zh
        }else{
          lang = lan.zh
        }
        this.buttonText = {
            today: lang.MAILBOX_COM_INNERBOX_TODAY,
            month: lang.CALENDAR_PAGE_CAL_MONTH,
            agendaWeek: lang.CALENDAR_PAGE_CAL_WEEK,
            agendaDay: lang.SETTING_RE_ADD_DAY,
            listMonth: lang.CALENDAR_PAGE_CAL_EVENT,
          }
        return lang
      }
    }
  }
</script>
<style scoped>
  .preview_box .el-loading-spinner i,.preview_box .el-loading-spinner .el-loading-text{
    color:#fff;
  }
  .content {
    width: 600px;
    height: 500px;
    margin: 100px auto auto auto;
    text-align: center;
  }
  .share-info {
    color: #777;
    padding-bottom: 18px;
}
  .share-box {
    position: relative;
    display: inline-block;
    height: 316px;
    width: 318px;
    background-image: url(../assets/img/file_share_box.jpg);
    background-position: center center;
    background-repeat: no-repeat;
}
  .file-info {
    position: absolute;
    width: 220px;
    top: 50px;
    left: 50px;
}
  .file-pwd {
    position: absolute;
    bottom: 40px;
    width: 100%;
}
  .file-info h4 {
    overflow: hidden;
    word-wrap: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
}
  .error {
    display: none;
    position: relative;
    text-align: left;
    padding: 0 0 12px 28px;
    color: #fe432d;
    font-weight: bold;
}
  .u-input {
    width: 180px;
    font-size: 14px;
    height: 18px;
    line-height: 18px;
    padding: 5px 10px;
    border: 1px solid #dddddd;
    outline: none;
    color: #999999;
    background: #ffffff !important;
    -webkit-transition: background-color 0.3s ease-out, border-color 0.3s ease-out, color 0.3s ease-out;
    transition: background-color 0.3s ease-out, border-color 0.3s ease-out, color 0.3s ease-out;
}
  .u-btn {
    display: inline-block;
    color: #FFFFFF;
    cursor: pointer;
    font-size: 13px;
    margin-left: 6px;
    width: 76px;
    height: 30px;
    text-align: center;
    transition: background-color ease-out 0.3s;
    background-color: #167ab6;
    border-radius: 3px;
    border: 1px solid #136b9f;
}
  .file-big-icon {
    background-image: url(../assets/img/file-sprite.png);
    display: inline-block;
}
  .folder_type{
    background-image: url(../assets/img/folder.jpg);
    display: inline-block;
    background-repeat: no-repeat;
    background-size:100%;
    background-position:0 0;
  }
  .file-invalid {
    position: relative;
    top: 52px;
}
  .round {
    background-color: #d34742;
    border-radius: 37px;
    top: 8px;
    width: 64px;
    height: 64px;
    display: inline-block;
    position: relative;
    margin-bottom: 18px;
    color:#fff;
    font-size:32px;
    line-height:64px;
}
</style>



